.header-wrap {
  height: 60px;
  background: #000;
  display: flex;
  align-items: center;
  position: relative;

  > * {
    cursor: pointer;
  }

  .icon-wrap{
    margin: 5px 5px 5px 10px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 5px;
    overflow: hidden;
    background: #413c3c;

    .icon{
      border-radius: 50%;
      width: 100%;
      height:100%;
    }
  }

  span{
    color: #FFF;
    font-size: 20px;
    font-weight: bold;
    margin-left: 10px;

    &.version{
      position: absolute;
      font-size: 8px;
      left: 131px;
      top: 32px;
      color: #5b5b5b;
      text-align: right;
    }
  }

}
